/**
 * @summary checkSize
 * @param {blob} file 
 * @param {int} checkSize  
 * @returns {boolean} isUnderCheckSize   true if file size is under checkSize; otherwise false
 * @note: 100m = 104857600B
 */

const checkFileSize  = (file, checkSize) => {
	return file.size <= checkSize;
}
export default checkFileSize